import React, { useEffect, useState } from "react";
import LalitLogo from "../../resources/LalitWeblogo.png";
import { FaBars } from "react-icons/fa";
import "./header.css";

function Header() {
  const [open, setOpen] = useState(false);
  const [open2, setOpen2] = useState(false);
  useEffect(() => {
    window.onClick = function (event) {
      console.log(event.target.matches("#navOnSmallScreen"));
      // if (!event.target.matches("#navOnSmallScreen")) {
      //   document.querySelector("#navOnSmallScreen").style.display = "none";
      // }
    };
  });
  function displayMenuForWideScreen() {
    let element = document.querySelector("#navList");
    if (open2) {
      element.style.display = "none";
    } else {
      element.style.display = "block";
    }
    setOpen2(!open2);
  }

  function displayMenu() {
    let element = document.querySelector("#navOnSmallScreen");
    if (open) {
      element.style.display = "none";
    } else {
      element.style.display = "block";
    }
    setOpen(!open);
  }
  return (
    <>
      <nav id="LargeScreen">
        <div>
          <img src={LalitLogo} alt="Logo" className="logo" />
        </div>
        <ul>
          <li>
            <div>
              <a href="/#about">About</a>
            </div>
          </li>
          <li>
            <div>
              <a href="/expertise">Expertise</a>
            </div>
          </li>
          <li>
            <a href="/#workHistory">Work History</a>
          </li>
          <li>
            <a href="/#education">Education</a>
          </li>
          <li id="navOnLargeScreen">
            <div
              // onMouseEnter={displayMenuForWideScreen}
              // onMouseLeave={displayMenuForWideScreen}
              onClick={displayMenuForWideScreen}
              id="dropDown"
            >
              Accounts
            </div>
            <ul id="navList">
              <li>
                <a
                  href="https://www.linkedin.com/in/advocate-lalit-k-sharma-ba461654/"
                  target="_blank"
                >
                  LinkedIn
                </a>
              </li>
              <li>
                <a
                  href="https://www.facebook.com/Lalit.Analyst"
                  target="_blank"
                >
                  Facebook
                </a>
              </li>
              <li>
                <a href="tel: +13652284324">Phone</a>
              </li>
            </ul>
          </li>
        </ul>
      </nav>
      <nav id="smallScreen">
        <img src={LalitLogo} alt="Logo" className="logo" />
        <FaBars size="35" onClick={displayMenu} id="dropDownMenu" />
        <ul id="navOnSmallScreen">
          <li>
            <a href="/#about">About</a>
          </li>
          <li>
            <a href="/expertise">Expertise</a>
          </li>
          <li>
            <a href="/#workHistory">Work History</a>
          </li>
          <li>
            <a href="/#education">Education</a>
          </li>
          <li>
            <a
              href="https://www.linkedin.com/in/advocate-lalit-k-sharma-ba461654/"
              target="_blank"
            >
              LinkedIn
            </a>
          </li>
          <li>
            <a href="https://www.facebook.com/Lalit.Analyst" target="_blank">
              Facebook
            </a>
          </li>
          <li>
            <a href="tel: +13652284324">Phone</a>
          </li>
        </ul>
      </nav>
    </>
  );
}

export default Header;
